<template>
  <div class="jarvis">
    <button @mousedown="startJarvis" @mouseup="stopJarvis">Hold to Talk</button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      recognition: null,
      isListening: false
    };
  },
  mounted() {
    this.initSpeechRecognition();
  },
  methods: {
    initSpeechRecognition() {
      // Use SpeechRecognition if available, otherwise fallback to webkitSpeechRecognition
      const SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition;
      
      if (!SpeechRecognition) {
        alert("Your browser does not support speech recognition.");
        return;
      }

      this.recognition = new SpeechRecognition();
      this.recognition.continuous = true; // Keep listening
      this.recognition.interimResults = false;
      this.recognition.lang = "en-US";

      this.recognition.onresult = (event) => {
        const transcript = event.results[event.results.length - 1][0].transcript;
        console.log("User said:", transcript);
        this.processCommand(transcript);
      };

      this.recognition.onerror = (event) => {
        console.error("Speech recognition error:", event.error);
      };

      this.recognition.onend = () => {
        if (this.isListening) {
          this.recognition.start(); // Restart if still active
        }
      };
    },

    startJarvis() {
      if (this.recognition) {
        this.isListening = true;
        this.recognition.start();
        console.log("Listening...");
      }
    },

    stopJarvis() {
      if (this.recognition) {
        this.isListening = false;
        this.recognition.stop();
        console.log("Stopped listening.");
      }
    },

    async processCommand(command) {
      try {
        const response = await fetch(
          "https://generativelanguage.googleapis.com/v1beta/models/gemini-1.5-flash:generateContent?key=AIzaSyAfCTFu1qXIGZoH5jwjwtB5Un5b456K5lY",
          {
            method: "POST",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify({ contents: [{ parts: [{ text: command }] }] }),
          }
        );

        const data = await response.json();
        const responseText = data.candidates?.[0]?.content?.parts?.[0]?.text || "I couldn't understand.";

        this.speak(responseText);
      } catch (error) {
        console.error("Error processing command:", error);
      }
    },

    async speak(text) {
      try {
        const voiceResponse = await fetch("https://api.elevenlabs.io/v1/text-to-speech/0sxRblex42me71A5Nbsy", {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            "xi-api-key": "sk_64c840ca1c724cd3747f04c4cddc4d2787697a086d209f19",
          },
          body: JSON.stringify({ text }),
        });

        const audioBlob = await voiceResponse.blob();
        const audioUrl = URL.createObjectURL(audioBlob);
        const audio = new Audio(audioUrl);
        audio.play();
      } catch (error) {
        console.error("Error in text-to-speech:", error);
      }
    }
  }
};
</script>

<style>
.jarvis {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

button {
  padding: 15px 30px;
  font-size: 18px;
  cursor: pointer;
  border: none;
  background: #007bff;
  color: white;
  border-radius: 5px;
}
</style>
